import window from 'global/window';
import _ from 'lodash';
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Logos from 'assets/images/InsurerLogos';
import { Col, Row } from 'reactstrap';

import Mouse from 'assets/images/FormLanding/Mouse.svg';
import InfoPage from '../../Templates/InfoPage/InfoPage';

import MainInfo from './MainInfo/MainInfo';
import BenefitsInfo from './BenefitsInfo/BenefitsInfo';
import WhyInfo from './WhyInfo/WhyInfo';
import TestimonialInfo from './TestimonialInfo/TestimonialInfo';
import DashboardInfo from './DashboardInfo/DashboardInfo';
// import MobileAppInfo from './MobileAppInfo/MobileAppInfo';
import HighlightInfo from './HighlightInfo/HighlightInfo';
import CoverageInfo from './CoverageInfo/CoverageInfo';
import QuestionsInfo from './QuestionsInfo/QuestionsInfo';
import GetCoverageInfo from './GetCoverageInfo/GetCoverageInfo';
import BusinessEnquiry from '../BusinessEnquiry/BusinessEnquiry';

import './BusinessLanding.scss';
import 'components/Templates/FormLanding/FormLandingNew.scss';
import 'components/Templates/FormLanding/FormLandingInformation.scss';
import 'components/Templates/FormLanding/FormLandingSplash.scss';

const insurerLogos = ['silicon_jungles', 'zave', 'antler'];

class BusinessLanding extends Component {
  constructor(props) {
    super(props);

    this.state = {
      showModal: false,
    };

    this.showEnquiryModal = this.showEnquiryModal.bind(this);
  }

  componentDidMount() {
    window.scrollTo(0, 0);
  }

  showEnquiryModal(showModal) {
    this.setState({ showModal });
  }

  renderMouse() {
    return (
      <Col xs={12} className="mouse d-none d-md-flex">
        <img src={Mouse} alt="Mouse.svg" />
      </Col>
    );
  }

  renderInsurerCarousel() {
    return (
      <Col className="form-landing-insurer-carousel carousel-container" xs={12}>
        <h5 className="insurer-carousel-label">PolicyPal Business clients include</h5>
        <Row className="insurer-logo-carousel" noGutters>
          {
            _.map(insurerLogos, logo => (
              <Col
                key={`business-insurer-${logo}`}
                className="logo"
                xs={6}
                sm={3}
              >
                <img src={Logos.square[logo]} alt={`${logo}.png`} />
              </Col>
            ))
          }
        </Row>
      </Col>
    );
  }

  render() {
    const { showModal } = this.state;
    const { transitionClass, handleAPI, isFromSession } = this.props;

    if (isFromSession) {
      return null;
    }

    return (
      <InfoPage
        className={['business-main-container', transitionClass].join(' ')}
        noPolicyPal
        subscribeOverwrite={
          <GetCoverageInfo
            {...this.props}
            showEnquiryModal={() => this.showEnquiryModal(true)}
          />
        }
        noAutoMargin
        mode="full"
      >
        <MainInfo
          {...this.props}
          showEnquiryModal={() => this.showEnquiryModal(true)}
        />
        {this.renderMouse()}
        {this.renderInsurerCarousel()}
        <WhyInfo />
        <BenefitsInfo
          {...this.props}
        />
        <HighlightInfo />
        <CoverageInfo />
        <DashboardInfo />
        <TestimonialInfo />
        <QuestionsInfo />
        <BusinessEnquiry
          showModal={showModal}
          hideEnquiryModal={() => this.showEnquiryModal(false)}
          handleAPI={handleAPI}
        />
      </InfoPage>
    );
  }
}

BusinessLanding.defaultProps = {
  isFromSession: false,
};

BusinessLanding.propTypes = {
  quoteButtonCallback: PropTypes.func.isRequired,
  transitionClass: PropTypes.string.isRequired,
  handleAPI: PropTypes.func.isRequired,
  isFromSession: PropTypes.bool,
};

export default BusinessLanding;
