import _ from 'lodash';
import React, { Component } from 'react';
import {
  Row,
  Col,
} from 'reactstrap';
import ReactSlickCarousel from 'components/Templates/ReactSlickCarousel/ReactSlickCarousel';

import Productivity from 'assets/images/Business/Main/Productivity.svg';
import Loyalty from 'assets/images/Business/Main/Loyalty.svg';
import Attendance from 'assets/images/Business/Main/Attendance.svg';

import './WhyInfo.scss';
import 'components/Templates/FormLanding/FormLandingNew.scss';
import 'components/Templates/FormLanding/FormLandingInformation.scss';

const whyData = [{
  icon: Productivity,
  title: 'Higher Productivity',
  desc: 'Your employees may have their own set of worries in their personal lives. When their issues beyond work are well taken care of, they become more focused at work.',
}, {
  icon: Loyalty,
  title: 'Increased Loyalty',
  desc: 'When you offer employee benefits, your employees are more likely to be happier & more loyal to your business.',
}, {
  icon: Attendance,
  title: 'Increased Attendance',
  desc: 'With health benefits, your employees are less likely to miss work due to health-related reasons.',
}];

class WhyInfo extends Component {
  renderItem(data, index) {
    const handleOnDragStart = e => e.preventDefault();

    return (
      <Col
        className="why-item"
        key={`business-why-info-${index}`}
        xs={12}
        md={4}
        onDragStart={handleOnDragStart}
      >
        <div className="why-info">
          <img src={data.icon} alt={`${data.title}.svg`} />
          <h5>{data.title}</h5>
          <p>{data.desc}</p>
        </div>
      </Col>
    );
  }

  render() {
    return (
      <Col xs={12}>
        <Row className="margin-padding why-container form-landing-information-container" noGutters>
          <Col xs={12}>
            <h2 className="main-title">WHY</h2>
            <h2 className="sub-title center">Why Employee Benefits?</h2>
          </Col>

          <Col xs={12}>
            <Row noGutters>
              <Col className="d-none d-lg-flex" xs={12}>
                <Row className="w-100">
                  {_.map(whyData, (data, index) => this.renderItem(data, index))}
                </Row>
              </Col>
              <Col className="slider-container d-block d-lg-none" xs={12}>
                <ReactSlickCarousel
                  lazyLoad
                  dots
                  centerMode
                  slidesToShow={1}
                  responsive={[{
                    breakpoint: 575,
                    settings: {
                      centerMode: true,
                      centerPadding: '50px',
                      slidesToShow: 1,
                    },
                  }, {
                    breakpoint: 991,
                    settings: {
                      centerMode: true,
                      centerPadding: '50px',
                      slidesToShow: 2,
                    },
                  }]}
                >
                  {_.map(whyData, (data, index) => this.renderItem(data, index))}
                </ReactSlickCarousel>
              </Col>
            </Row>
          </Col>
        </Row>
      </Col>
    );
  }
}

export default WhyInfo;
