import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {
  Row,
  Col,
} from 'reactstrap';

import Benefits from 'assets/images/Business/Main/Benefits.svg';
import Umbrella from 'assets/images/Business/Main/Umbrella.svg';

import Button from 'components/FormsV3/Button/Button';

import './BenefitsInfo.scss';
import 'components/Templates/FormLanding/FormLandingNew.scss';
import 'components/Templates/FormLanding/FormLandingInformation.scss';

class BenefitsInfo extends Component {
  render() {
    const {
      quoteButtonCallback,
    } = this.props;

    return (
      <Col className="benefits-container" xs={12}>
        <Row className="margin-padding benefits-inner-container form-landing-information-container" noGutters>
          <Col xs={12}>
            <h2 className="main-title">BENEFITS</h2>
            <h2 className="sub-title center">Why Choose PolicyPal Business?</h2>
          </Col>

          <Col xs={12}>
            <Row className="benefits-content" noGutters>
              <Col xs={12} lg={{ offset: 1, size: 5 }}>
                <Col xs={12}>
                  <Row noGutters className="highlight-container">
                    <p className="highlight-text">Fully Integrated</p>
                    <img src={Umbrella} alt="Umbrella.svg" />
                  </Row>
                </Col>
                <Col xs={12}>
                  <h1>PolicyPal Business</h1>
                  <h6 className="description">EZCare PAL is an easy-to-understand Employee Benefits solution. We offer customisable plans and provide access to the best health care services.</h6>

                  <div className="promo-button">
                    <Button
                      variant="medium-flat-green"
                      height={48}
                      width={130}
                      onClick={() => quoteButtonCallback(true)}
                    >
                      <p>Get Quote</p>
                    </Button>
                  </div>
                </Col>
              </Col>
              <Col xs={12} lg={{ size: 5 }} className="benefits-img">
                <img src={Benefits} alt="Benefits.svg" />
              </Col>
            </Row>
          </Col>
        </Row>
      </Col>
    );
  }
}

BenefitsInfo.propTypes = {
  quoteButtonCallback: PropTypes.func.isRequired,
};

export default BenefitsInfo;
