import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Col, Row } from 'reactstrap';

// import Button from 'components/FormsV3/Button/Button';
import EmailInput from 'components/FormsV3/EmailInput/EmailInput';
import Table from 'assets/images/Business/Main/Table.svg';

import './GetCoverageInfo.scss';
import 'components/Templates/FormLanding/FormLandingNew.scss';
import 'components/Templates/FormLanding/FormLandingInformation.scss';

class GetCoverageInfo extends Component {
  render() {
    const {
      onChangeValue,
      values,
      errors,
      quoteButtonCallback,
      emailField,
      showEnquiryModal,
    } = this.props;

    return (
      <Col className="mx-auto px-4" xs={12}>
        <Row className="margin-padding get-coverage-container" noGutters>
          <Col className="left-container" xs={12} lg={6}>
            <h2 className="sub-title center">Get Coverage for your Business Today!</h2>
            {/* <div className="learn-more-button">
              <Button
                variant="medium-flat-green"
                height={48}
                width={250}
                onClick={() => {
                  if (quoteButtonCallback) {
                    quoteButtonCallback(true);
                  }
                }}
              >
                <p>Learn More</p>
              </Button>
            </div> */}
            <EmailInput
              placeholderString={emailField.placeholderString}
              name={emailField.slug}
              buttonText="Get Quote"
              value={(values && values[emailField.slug] && values[emailField.slug].value) || ''}
              errorString={errors && errors[emailField.slug]}
              onChange={onChangeValue}
              onClick={() => quoteButtonCallback(false)}
            />
            <h5 className="footnote">
              Enquiries? Click
              <button
                className="text-link"
                onClick={showEnquiryModal}
              >
                here
              </button>
            </h5>
          </Col>
          <Col className="right-container" xs={12} lg={6}>
            <div className="table-img">
              <img src={Table} alt="Table.svg" />
            </div>
          </Col>
        </Row>
      </Col>
    );
  }
}


GetCoverageInfo.defaultProps = {
  values: null,
  errors: null,
};

GetCoverageInfo.propTypes = {
  onChangeValue: PropTypes.func.isRequired,
  values: PropTypes.object,
  errors: PropTypes.object,
  quoteButtonCallback: PropTypes.func.isRequired,
  emailField: PropTypes.object.isRequired,
  showEnquiryModal: PropTypes.func.isRequired,
};

export default GetCoverageInfo;
