import React, { Component } from 'react';
import { Col, Row } from 'reactstrap';

import Claire from 'assets/images/Business/Main/Claire.png';
import Whatsapp from 'assets/images/MarketPlace/Logo/Whatsapp.svg';

import './QuestionsInfo.scss';
import 'components/Templates/FormLanding/FormLandingNew.scss';
import 'components/Templates/FormLanding/FormLandingInformation.scss';

class QuestionsInfo extends Component {
  renderWhatsapp(renderDisclaimer, isBigWhatsapp) {
    const whatsappStyle = isBigWhatsapp ? 'big-whatsapp' : 'small-whatsapp';

    return (
      <div className={['whatsapp-container', whatsappStyle].join(' ')}>
        <img src={Whatsapp} alt="whatsapp.svg" />
        <div className="whatsapp-inner">
          <div className="whatsapp">
            <h4>Whatsapp us at</h4>
            <a href="https://wa.me/6587500688" target="__blank">+65 8750 0688</a>

            <h4>Call us at</h4>
            <a href="tel:+6531639184">+65 3163 9184</a>
          </div>
        </div>
      </div>
    );
  }

  render() {
    return (
      <Col xs={12}>
        <Row className="questions-container margin-padding" noGutters>
          <Col className="business-img-container" xs={4} sm={4} lg={6}>
            <img className="business-img" src={Claire} alt="Claire.png" />
          </Col>
          <Col xs={8} sm={8} lg={6} className="contents">
            <h2>Real people to answer all your questions.</h2>
            <p>
              Business Insurance is complicated. We want
              you to be covered and feel secure, without
              paying for more than you need.
            </p>
            <div className="whatsapp-non-resp">
              {this.renderWhatsapp()}
            </div>
          </Col>
          <Col xs={12} className="contents whatsapp-resp">
            {this.renderWhatsapp()}
          </Col>
        </Row>
      </Col>
    );
  }
}

export default QuestionsInfo;
