/**
 * TravelDetail.js
 * Detail View for travel
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

import FieldGenerator from 'components/FormsV3/FieldGenerator';
import Button from 'components/FormsV3/Button/Button';
import ModalPopup from 'components/Templates/ModalPopup/ModalPopup';
import CheckoutComplete from 'components/Templates/CheckoutPopup/CheckoutComplete';

import { sendEmailForm } from 'actions/insuranceActions';

import { validateFormValue, validateForm } from 'lib/validatorsV2';
import { ampTag } from 'lib/tagFunctions';

import './BusinessEnquiry.scss';

const interestsData = [{
  size: 6,
  title: 'Employee Benefits',
  slug: 'employee_benefits',
  type: 'checkbox',
}, {
  size: 6,
  title: 'Cyber Risk Insurance',
  slug: 'cyber_risk_insurance',
  type: 'checkbox',
}, {
  size: 6,
  title: 'Directors & Officers Insurance',
  slug: 'directors_officers_insurance',
  type: 'checkbox',
}, {
  size: 6,
  title: 'Professional Indemnity Insurance',
  slug: 'professional_indemnity_insurance',
  type: 'checkbox',
}, {
  size: 6,
  title: 'Public Liability',
  slug: 'public_liability',
  type: 'checkbox',
}, {
  size: 6,
  title: 'Work Injury Compensation',
  slug: 'work_injury_compensation',
  type: 'checkbox',
}];

const forms = [
  {
    desc: 'Details',
    size: 12,
    title: 'Email',
    slug: 'email',
    type: 'email',
    is_required: true,
  }, {
    size: 6,
    title: 'Full name',
    slug: 'name',
    type: 'text',
    is_required: true,
  }, {
    size: 6,
    title: 'Mobile number',
    slug: 'contact',
    type: 'number',
    is_required: true,
  }, {
    size: 12,
    title: 'Drop your enquiry here',
    slug: 'note',
    type: 'textarea',
    is_required: true,
  }];

const allForms = [...interestsData, ...forms];

class BusinessEnquiry extends Component {
  constructor(props) {
    super(props);

    this.state = {
      values: {},
      errors: {},
    };

    this.onChangeValue = this.onChangeValue.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.hideModal = this.hideModal.bind(this);
  }

  // Update form values on change
  async onChangeValue(type, name, value) {
    const { values, errors } = this.state;

    const error = errors && errors[name];
    let newError = {};

    if (error) {
      newError = { [name]: await validateFormValue(name, { type, value }, values, allForms) };
    }

    this.setState({
      values: {
        ...values,
        [name]: { type, value },
      },
      errors: { ...errors, ...newError },
    });
  }

  async handleSubmit() {
    const { values } = this.state;
    const { handleAPI, errors } = this.props;

    try {
      const fieldsToCheck = [
        // 'interests',
        'name',
        'contact',
        'email',
        'note',
      ];

      const newErrors = await validateForm(values, fieldsToCheck, allForms);

      if (!_.isEmpty(newErrors)) {
        this.setState({
          errors: newErrors,
        });

        return;
      }

      ampTag('BizSubmitA', {
        note: values.note.value,
        email: values.email.value,
      });

      const requestData = {
        form_category: 'Business Enquiry',
        input_data: _.map(forms, f => ({
          name: f.title,
          value: values[f.slug].value,
          type: 'text',
        })),
      };

      const interests = _.reduce(interestsData, (result, interest) => {
        try {
          if (values[interest.slug].value) {
            result.push(interest.title);
          }
        } catch (error) {
          // eslint-disable-next-line
        }

        return result;
      }, []);

      requestData.input_data.push({
        name: 'Interests',
        value: _.isEmpty(interests) ? ['N/A'] : interests,
        type: 'array',
      });

      const result = await handleAPI(() => sendEmailForm(requestData));

      if (result) {
        if (this.checkoutComplete) {
          setTimeout(() => {
            this.checkoutComplete.openCheckoutComplete();
            ampTag('BizConfirmationS');
          }, 300);
        }
      } else {
        this.setState({
          errors: {
            ...errors,
            note: 'Request failed! Please try again.',
          },
        });
      }
    } catch (error) {
      // eslint-disable-next-line
    }
  }

  hideModal() {
    const { hideEnquiryModal } = this.props;

    this.setState({
      values: {},
      errors: {},
    });

    if (hideEnquiryModal) {
      hideEnquiryModal();
    }
  }

  render() {
    const { values, errors } = this.state;
    const { showModal } = this.props;

    try {
      return (
        <div className="business-enquiry-container">
          <ModalPopup
            showModal={showModal}
            closeModal={this.hideModal}
            className="business-enquiry-modal"
            backdropClassName="blue-modal-backdrop"
            header={<p>Submit enquiry</p>}
            body={
              <Row noGutters>
                <Col xs={12}>
                  <div className="input-wrapper-main-v3">
                    <p className="input-wrapper-desc">Choose your topic (select all that apply)</p>
                  </div>
                </Col>
                {
                  _.map(allForms, (form, index) => (
                    <FieldGenerator
                      key={`business-form-${index}`}
                      values={values}
                      errors={errors}
                      fieldData={form}
                      onChangeValue={this.onChangeValue}
                      extraInfo={form.extra_info && <p>{form.extra_info}</p>}
                    />
                  ))
                }
              </Row>
            }
            footer={
              <Button
                variant="medium-flat-green"
                height={48}
                width={210}
                onClick={this.handleSubmit}
              >
                <p>Proceed to get quote</p>
              </Button>
            }
          />
          <CheckoutComplete
            className="blue-popup-backdrop"
            isQuoteOnly
            upperText="Submission Successful!"
            midText="Thank You for Choosing PolicyPal! We will be contacting you within 48 hours."
            buttonText="Confirm"
            wrappedComponentRef={node => this.checkoutComplete = node}
            callback={() => {
              this.setState({
                values: {},
                errors: {},
              });

              this.hideModal();
            }}
          />
        </div>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }
}

BusinessEnquiry.defaultProps = {
};

BusinessEnquiry.propTypes = {
  showModal: PropTypes.bool.isRequired,
  hideEnquiryModal: PropTypes.func.isRequired,
  handleAPI: PropTypes.func.isRequired,
};

export default BusinessEnquiry;
