import React, { Component } from 'react';
import {
  Row,
  Col,
} from 'reactstrap';
import _ from 'lodash';
import ReactSlickCarousel from 'components/Templates/ReactSlickCarousel/ReactSlickCarousel';


// import appstore and playstore logo
import Simple from 'assets/images/Business/Main/Simple.svg';
import Online from 'assets/images/Business/Main/Online.svg';
import Immediate from 'assets/images/Business/Main/Immediate.svg';
import Highlight from 'assets/images/Business/Main/Highlight.svg';
import Grandpa from 'assets/images/Business/Main/Grandpa.svg';
import InvertedCommaPurple from 'assets/images/Business/Main/InvertedCommaPurple.svg';
// import InvertedCommaWhite from 'assets/images/Business/Main/InvertedCommaWhite.svg';

import './HighlightInfo.scss';
import 'components/Templates/FormLanding/FormLandingNew.scss';
import 'components/Templates/FormLanding/FormLandingInformation.scss';

const highlightData = [{
  icon: Simple,
  title: 'Simple',
  desc: 'Get covered in three easy steps',
}, {
  icon: Online,
  title: '100% Online',
  desc: 'Yay to zero forms & paperwork',
}, {
  icon: Immediate,
  title: 'Immediate',
  desc: 'Get immediate provisional cover',
}];

class HighlightInfo extends Component {
  renderItem(data, index) {
    const handleOnDragStart = e => e.preventDefault();

    return (
      <Col
        key={`business-highlight-info-${index}`}
        xs={12}
        md={4}
        onDragStart={handleOnDragStart}
      >
        <div className="highlight-info">
          <img src={data.icon} alt={`${data.title}.svg`} />
          <h5>{data.title}</h5>
          <p>{data.desc}</p>
        </div>
      </Col>
    );
  }

  render() {
    return (
      <Col xs={12} className="highlight-outer-container">
        <Row className="w-100" noGutters>
          <Col className="highlight-container" xs={12}>
            <Row className="margin-padding form-landing-information-container" noGutters>
              <Col xs={12} className="quote-main">
                {/* <img src={InvertedCommaWhite} className="inverted-comma-top" alt="invertedComma" /> */}
                <div className="cursive-title">
                  The Future of
                  <span className="green"> Business Insurance</span>
                </div>
                {/* <img src={InvertedCommaWhite} className="inverted-comma-bottom" alt="invertedComma" /> */}
              </Col>
              <Col className="d-none d-md-flex" xs={12}>
                <Row className="highlight-items" noGutters>
                  {_.map(highlightData, (data, index) => this.renderItem(data, index))}
                </Row>
              </Col>
              <Col className="slider-container d-block d-md-none" xs={12}>
                <ReactSlickCarousel
                  lazyLoad
                  dots
                  centerMode
                  slidesToShow={1}
                  responsive={[{
                    breakpoint: 575,
                    settings: {
                      centerMode: true,
                      centerPadding: '50px',
                      slidesToShow: 1,
                    },
                  }, {
                    breakpoint: 991,
                    settings: {
                      centerMode: true,
                      centerPadding: '50px',
                      slidesToShow: 2,
                    },
                  }]}
                >
                  {_.map(highlightData, (data, index) => this.renderItem(data, index))}
                </ReactSlickCarousel>
              </Col>
            </Row>
          </Col>
          <Col className="details-container" xs={12}>
            <Row className="margin-padding form-landing-information-container">
              <Col xs={12} md={6} className="highlight-img">
                <img src={Highlight} alt="Highlight.svg" />
              </Col>
              <Col xs={12} md={6} className="quote">
                <img src={InvertedCommaPurple} className="inverted-comma-top" alt="invertedComma" />
                <h5>
                  Traditional Employee Benefits application requires a manual
                  process which is time consuming for businesses. Without a
                  platform for your employees, HR often have to attend to their
                  enquiries regarding insurance information.
                </h5>
                <img src={InvertedCommaPurple} className="inverted-comma-bottom" alt="invertedComma" />
              </Col>
            </Row>
          </Col>
          <div className="grandpa-img">
            <img src={Grandpa} alt="Grandpa.svg" />
          </div>
        </Row>
      </Col>
    );
  }
}

export default HighlightInfo;
