import React, { Component } from 'react';
import { Col, Row } from 'reactstrap';
import PropTypes from 'prop-types';

import Sponsors from 'components/Landing/LandingMASSection/Sponsors';
import Shoutout from 'assets/images/Business/Main/Shoutout.svg';

// Footer
import Testimonal from './Testimonial/Testimonial';

import './TestimonialInfo.scss';
import 'components/Templates/FormLanding/FormLandingNew.scss';
import 'components/Templates/FormLanding/FormLandingInformation.scss';

class TestimonialInfo extends Component {
  render() {
    const { noLogos, slickProps } = this.props;

    return (
      <Col className="form-landing-insurer-carousel testimonial-container" xs={12}>
        <Testimonal
          slickProps={slickProps}
        />
        {
          !noLogos &&
          <Row className="channel-list-container" noGutters>
            <Col xs={12} className="channel-list">
              <img src={Shoutout} alt="Shoutout.svg" className="shoutout-img" />
              <div className="channels">
                <Sponsors />
              </div>
            </Col>
          </Row>
        }
      </Col>
    );
  }
}

TestimonialInfo.defaultProps = {
  slickProps: null,
  noLogos: false,
};

TestimonialInfo.propTypes = {
  slickProps: PropTypes.object,
  noLogos: PropTypes.bool,
};


export default TestimonialInfo;
