import _ from 'lodash';
import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { Col, Row } from 'reactstrap';
import ReactSlickCarousel from 'components/Templates/ReactSlickCarousel/ReactSlickCarousel';

import AboutImages from 'assets/images/About';


import './Testimonial.scss';

const testimonialData = [{
  title: 'This is Albert',
  subTitle: 'Company',
  desc: 'It was great working with the PolicyPal team! You guys were quick to revert and so helpful with every request. Definitely looking forward to working with you again.',
}, {
  title: 'Zave',
  subTitle: 'Company',
  desc: 'The team at PolicyPal posses the in-depth knowledge in the products that they are offering. They take time to understand our needs and advised us accordingly to what best suited for our organisation. As a business owner, I appreciate this approach. Their app is user-friendly and it allows me to see all of our policies at a glance. It is truly a pleasure doing business with you.',
}, {
  title: 'Felicia',
  subTitle: 'HR Manager',
  desc: 'Team PolicyPal is very fast in their responses! They are very knowledgeable about the different insurance products and are able to advice me on the right coverage I should get for my company.\n\nI love them so much! XOXO',
}];

class Testimonial extends Component {
  renderItem(data, index, isLast) {
    const { theme } = this.props;
    const imageSrc = theme !== 'default' ? AboutImages.InvertedCommaBlue : AboutImages.InvertedComma;

    const handleOnDragStart = e => e.preventDefault();

    return (
      <Col
        className="testimonial-item-container"
        key={`testimonial-item-${index}`}
        xs={4}
        onDragStart={handleOnDragStart}
      >
        <div className="testimonial-item">
          <p className="desc">{data.desc}</p>
          <p className="title">{data.title}</p>
          <p className="sub-title">{data.subTitle}</p>
        </div>
        {
          isLast &&
          <div className="comma-down d-none d-lg-block">
            <img src={imageSrc} alt="invertedComma" />
          </div>
        }
      </Col>
    );
  }

  render() {
    const { theme, slickProps } = this.props;
    const imageSrc = theme !== 'default' ? AboutImages.InvertedCommaBlue : AboutImages.InvertedComma;

    return (
      <Row className="testimonial w-100" noGutters>
        <Col className="header-container" xs={12} lg={10}>
          <div className="comma-up">
            <img src={imageSrc} alt="invertedComma" />
          </div>
          <div className="header">
            <p>We are serving 50,000 individuals since 2016</p>
            <h4>And they enjoyed it</h4>
          </div>
        </Col>
        <Col className="d-none d-lg-flex" xs={12}>
          <Row>
            {_.map(testimonialData, (data, index) => this.renderItem(data, index, index === testimonialData.length - 1))}
          </Row>
        </Col>
        <Col className="slider-container d-block d-lg-none" xs={12}>
          <ReactSlickCarousel
            lazyLoad
            dots
            slidesToShow={1}
            responsive={[{
              breakpoint: 575,
              settings: {
                slidesToShow: 1,
              },
            }, {
              breakpoint: 991,
              settings: {
                slidesToShow: 2,
              },
            }]}
            {...slickProps}
          >
            {_.map(testimonialData, (data, index) => this.renderItem(data, index, index === testimonialData.length - 1))}
          </ReactSlickCarousel>
          <div className="comma-down d-block d-lg-none">
            <img src={imageSrc} alt="invertedComma" />
          </div>
        </Col>
      </Row>
    );
  }
}


Testimonial.defaultProps = {
  theme: 'blue',
  slickProps: {},
};

Testimonial.propTypes = {
  theme: PropTypes.string,
  slickProps: PropTypes.object,
};

export default Testimonial;
