import _ from 'lodash';
import React, { Component } from 'react';
import {
  Row,
  Col,
} from 'reactstrap';

import Profile from 'assets/images/Business/Main/Profile.png';
import Tick from 'assets/images/Business/Main/Tick.svg';

import './DashboardInfo.scss';
import 'components/Templates/FormLanding/FormLandingNew.scss';
import 'components/Templates/FormLanding/FormLandingInformation.scss';

const dashboardData = [{
  title: 'Onboard your company in less than five minutes.',
  desc: '',
}, {
  title: 'Set up your company profile easily.',
  desc: '',
}, {
  title: 'Manage your employees\' entries and exits all in one place.',
  desc: 'Add and remove your employees from your list of beneficiaries.',
}];

class DashboardInfo extends Component {
  render() {
    return (
      <Col xs={12}>
        <Row className="margin-padding dashboard-container form-landing-information-container" noGutters>
          <Col className="header-container" xs={12}>
            <h2 className="main-title">HR DASHBOARD</h2>
            <h2 className="sub-title center">Easy to Manage with Our Dashboard</h2>
          </Col>
          <Col xs={12} lg={8}>
            <div className="profile-img">
              <img src={Profile} alt="Profile.png" />
            </div>
          </Col>
          <Col xs={12} lg={4} className="dashboard-info-container">
            <Row>
              {/* <Col className="dashboard-header" xs={12}>
                <h4>No paperwork. No confusion</h4>
                <p>
                  Easy to use online portal for adding
                  employees, paying your bill, seeing your
                  plans and more!
                </p>
              </Col> */}
              {
                _.map(dashboardData, (d, index) => (
                  <Col
                    xs={12}
                    key={`business-dashboard-info-${index}`}
                    className="dashboard-info"
                  >
                    <img src={Tick} alt="Tick.svg" />
                    <div>
                      <h5>{d.title}</h5>
                      <p>{d.desc}</p>
                    </div>
                  </Col>
                ))
              }
            </Row>
          </Col>
        </Row>
      </Col>
    );
  }
}

export default DashboardInfo;
