import _ from 'lodash';
import React, { Component } from 'react';
import {
  Row,
  Col,
} from 'reactstrap';
import ReactSlickCarousel from 'components/Templates/ReactSlickCarousel/ReactSlickCarousel';

import GP from 'assets/images/Business/Main/GP.svg';
import Dental from 'assets/images/Business/Main/Dental.svg';
import Outpatient from 'assets/images/Business/Main/Outpatient.svg';
import Coverage from 'assets/images/Business/Main/Coverage.svg';

import './CoverageInfo.scss';
import 'components/Templates/FormLanding/FormLandingNew.scss';
import 'components/Templates/FormLanding/FormLandingInformation.scss';

const coverageData = [{
  icon: GP,
  title: 'General Practitioner',
  desc: 'Cover 100% of your employees\' General Practitioner Clinic expenses, consultations and medication from a panel of selected doctors.',
}, {
  icon: Outpatient,
  title: 'Outpatient Specialists',
  desc: 'Secure your employees for all their major specialist treatments. They will be reimbursed up to an annual amount that you define. ',
}, {
  icon: Dental,
  title: 'Dental Cover',
  desc: 'Covers routine dental visits, consultations, amalgam restorations (fillings), extractions, root canal treatments and periodontal treatments.',
}];

class CoverageInfo extends Component {
  renderItem(data, index) {
    const handleOnDragStart = e => e.preventDefault();

    return (
      <Col
        key={`business-coverage-info-${index}`}
        xs={12}
        md={4}
        onDragStart={handleOnDragStart}
      >
        <div className="coverage-info">
          <img src={data.icon} alt={`${data.title}.svg`} />
          <div>
            <h5>{data.title}</h5>
            <p>{data.desc}</p>
          </div>
        </div>
      </Col>
    );
  }

  render() {
    return (
      <Col xs={12}>
        <Row className="margin-padding coverage-container form-landing-information-container" noGutters>
          <Col xs={12}>
            <h2 className="main-title">COVERAGE</h2>
            <h2 className="sub-title center">What are you covered for?</h2>
          </Col>

          <Row className="w-100" noGutters>
            <Col xs={12} md={{ offset: 1, size: 6 }} className="coverage-img">
              <img src={Coverage} alt="Coverage.svg" />
            </Col>
            <Col xs={12} md={4} className="highlight">
              <h2>Group Hospitalisation</h2>
              <p>Configure an affordable plan for your employees.</p>
              <br />
              <p>&#x2022; Select between private and public hospitals.</p>
              <p>&#x2022; Choose the ward type.</p>
            </Col>
          </Row>

          <Col xs={12}>
            <h5 className="add-on">
              Looking for more? <br /> You can choose to add-on the following:
            </h5>
          </Col>

          <Col className="d-none d-md-flex" xs={12}>
            <Row className="w-100" noGutters>
              {_.map(coverageData, (data, index) => this.renderItem(data, index))}
            </Row>
          </Col>

          <Col className="slider-container d-block d-md-none" xs={12}>
            <ReactSlickCarousel
              lazyLoad
              dots
              centerMode
              slidesToShow={1}
              responsive={[{
                breakpoint: 575,
                settings: {
                  centerMode: true,
                  centerPadding: '50px',
                  slidesToShow: 1,
                },
              }, {
                breakpoint: 991,
                settings: {
                  centerMode: true,
                  centerPadding: '50px',
                  slidesToShow: 2,
                },
              }]}
            >
              {_.map(coverageData, (data, index) => this.renderItem(data, index))}
            </ReactSlickCarousel>
          </Col>
        </Row>
      </Col>
    );
  }
}

export default CoverageInfo;
