import _ from 'lodash';
import React, { Component } from 'react';

import { Row, Nav, NavItem, NavLink } from 'reactstrap';

// import ArrayObject from Sponsor Logos
import Logos from 'assets/images/Sponsor';

class Sponsors extends Component {
  constructor(props) {
    super(props);

    this.renderSponsor = this.renderSponsor.bind(this);
  }
  renderSponsor() {
    return _.map(Logos.sponsors, (sponsor, index) => (
      <NavItem key={index}>
        <NavLink href={sponsor.url}>
          <img src={sponsor.src} alt={`logo-${index}`} />
        </NavLink>
      </NavItem>
    ));
  }
  render() {
    return (
      <Nav className="sponsor">
        <Row className="wrapper">{this.renderSponsor()}</Row>
      </Nav>
    );
  }
}

export default Sponsors;
